import { Highlight } from "react-instantsearch";

export default function DisplayHits({hit}) {
    return (
        <article className="HitContainer">
            <img src={hit.image} alt={hit.name} className="HitImage"/>
            <h3 className="HitName">
                <Highlight attribute="name" hit={hit} />
            </h3>
            <p className="HitCategories">{hit.categories[0]}</p>
            <p className="HitPrice">{hit.price}</p>
        </article>
    );
};