import "@fontsource/libre-baskerville"; 
import './App.css';
import 'instantsearch.css/themes/algolia.css';
import algoliasearch from 'algoliasearch';
import DisplayHits from "./DisplayHits";
import { 
  InstantSearch, 
  SearchBox, 
  Hits, 
  RefinementList, 
  CurrentRefinements, 
  Pagination, 
  Configure 
} from 'react-instantsearch';

const applicationID = "PQDDGP76DS";
const searchOnlyAPIKey = "16829e4325d8e4aaed767ff3021b8fbc" ;
const searchClient = algoliasearch(applicationID, searchOnlyAPIKey);


function App() {
  return (
    <div className="grid-container">
        <InstantSearch searchClient={searchClient} indexName="demoIndex" >
          <Configure hitsPerPage={20} />

          <SearchBox className="SearchBox" placeholder='Search experience powered by Algolia' />
          <div className="Filters">
            <CurrentRefinements />
              <p>BRANDS</p>
              <RefinementList 
                attribute="brand" 
                showMore="true" 
                limit={10} 
                showMoreLimit={100} 
                searchable={true} 
                searchablePlaceholder="Search our brands"
                sortBy={['count:desc', 'name:asc']}
              />
              <p>CATEGORIES</p>
              <RefinementList 
                attribute="categories" 
                showMore="true" 
                limit={5} 
                showMoreLimit={20} 
                searchable={true} 
                searchablePlaceholder="Search our categories"
              />
          </div>
          
          <div className="SearchList">
            <Hits hitComponent={DisplayHits} />
            <Pagination />
          </div>
        </InstantSearch>
    </div>
  );
}

export default App;
